@include custommq($min: 48rem) {
	[data-sidebar-style="mini"] {        
		--ic-sidebar-width: 70px;
		--ic-sidebar-pos: absolute;
		--ic-side-nav-padding: 1.125rem 0.875rem;
		--ic-side-info: none;
		--ic-side-copyright: none;
	}
}


@each $name, $color in $theme_backgrounds{
	[data-sidebarbg="#{$name}"] {
        @if $name != "color_1"{		
			&[data-sidebar-style="modern"]{
				.icnav{
					.metismenu li ul{
						background-color: lighten($color: $color, $amount: 5%);
					}
				}
			}
		}
	}
}
@include custommq($min: 48rem){
    [data-sidebar-style="mini"] {
        .icnav-scroll{
			overflow: visible;
		}
		.icnav .metismenu .has-arrow:after,
		.icnav .metismenu .mm-active > .has-arrow:after{
			right: 25px;
			transform: rotate(-225deg) translateY(-50%);
		}
		.icnav .metismenu{
			& > li > a{
				text-align: center;
				
				i {
					margin-right: 0;
					margin-top: 0;
					font-size: 20px;
				}
			}					
			.nav-text {
				display: none;
			}
			.menu-title{
				display:none;
			}
			.nav-label,
			.nav-badge{
				display: none;
			}
			ul {
				padding: 15px 0;
				position: absolute;
				left: 100%;
				background-color: #000;
				border-radius: 0 var(--bs-border-radius) var(--bs-border-radius) 0;
				width: 175px;
				transform: translateX(-5px);
				transition: all 0.2s;
				visibility: hidden;
				opacity: 0;
				display: block !important;
				height: auto !important;
				top: 0;
				
				ul{
					border-radius: var(--bs-border-radius);
				}
				a{
					padding: 10px 25px;
					
					&::before{
						content: none;
					}
				}
			}
			& > li{
				& > a{
					padding: 12px 15px;
					
					&.has-arrow {
						&:after {
							display: none;
						}
					}
				}
				&:hover{
					& > ul{
						transform: translateX(0);
						opacity: 1;
						visibility: visible;
					}
				}
				& > ul > li:hover > ul{
					transform: translateX(0);
					opacity: 1;
					visibility: visible;
				}
			}
		}
		.nav-header{
            .nav-control {
                z-index: -1;
            }
            .brand-title {
                display: none;
            }
            .hamburger{
                display: none;
            }
        }
    }
	[data-sidebar-style="mini"][data-layout="vertical"]{
		.icnav .metismenu > li:nth-child(n+8) {
			ul {
				top: auto;
				bottom: 0;
			}
		}
	}
}