  margin-bottom: 0;
  align-items: center;
}
.page-title .breadcrumb li {
  margin-top: 0;
  margin-bottom: 0;
}
.page-title .breadcrumb li a {
  display: flex;
  gap: 6px;
  color: var(--bs-gray-300);
}
.page-title .breadcrumb li.active {
  color: var(--bs-gray-300);
  font-weight: 500;
}
.page-title .breadcrumb li.active a {
  color: var(--bs-gray-300) !important;
}
